export default {
	data() {
		return {
			active: 1,
			activityList: [],
			modeList: [
				{
					label: '线下活动',
					value: 1,
				},
				{
					label: '线上直播',
					value: 2,
				},
			],
		}
	},
	onLoad() {
		this.getActivityList();
		uni.startPullDownRefresh();
	},
	methods: {
		toggle(item) {
			this.active = item;
			this.getActivityList();
		},
		getActivityList() {
			let that = this;
			var query = this.$Bmob.Query('activity');
			this.activityList = [];
			let arr = [];
			
			query.order('-updatedAt');
			if (this.active) {
				query.equalTo('sort', '==', 1);
			} else {
				query.equalTo('sort', '!=', 1);
			}
			query.find().then((res) => {
				uni.stopPullDownRefresh();
				// that.loading = false;
				for (let i = 0; i < res.length; i += 1) {
					for (let key in res[i].startTime) {
						if (key === 'iso') {
							res[i].startTime = res[i].startTime[key];
						}
					}
					for (let key in res[i].endTime) {
						if (key === 'iso') {
							res[i].endTime = res[i].endTime[key];
						}
					}
					arr.push({
						id: res[i].objectId,
						src: res[i].imgSrc,
						title: res[i].title,
						// desc: res[i].desc,
						// startTime: that.$moment(res[i].startTime).format('YYYY-MM-DD HH:mm'),
						// endTime: that.$moment(res[i].endTime).format('YYYY-MM-DD HH:mm'),
						number: res[i].number,
						mode: that.modeList[res[i].mode - 1].label,
						fee: res[i].fee,
					});
				}
				that.activityList = arr;
			});
		},
		onPullDownRefresh() {
				console.log('refresh');
				// setTimeout(function () {
				// 		uni.stopPullDownRefresh();
				// }, 1000);
				this.getActivityList();
		}
	}
}
